.App {
    padding: 30px;
    display: flex;
}

.App > div {
    margin-right: 50px;
}

input {
    outline: none;
}

.error {
    color: red;
}

input.error {
    border: 2px solid red;
}

.active-filter {
    background-color: gold;
}

button {
    background-color: #f1e8bf;
    border: none;
    border-radius: 10%;
    padding: 5px;
    margin: 5px;
}

.is-done {
    opacity: 0.5;
}